@import '../../../scss/variables';

.modal {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: $color-white;
  z-index: 100000;
  display: none;
  top: 0;
  left: 0;
  right: 0; 
  bottom: 0;
  box-sizing: border-box;
}

.modal__content {
  width: 984px;
  height: 647px;
  margin: 0 auto;
  position: relative;
  align-self: center;
  padding: 81px 242px 0 242px;
}

.modal__title {
  font-size: 40px;
  position: absolute;
  background-color: $color-white;
  width: 696px;
  top: -19px;
  left: 144px;
  text-align: center;
  font-weight: 500;
}

.modal__image {
  display: block;
  margin: 140px auto 0 auto;
  width: 214px;
  height: 148px;
}

.modal__text {
  text-transform: uppercase;
  width: 275px;
  font-weight: 400;
  text-align: center;
  margin: 40px auto 0 auto;
}

